<template>
    <section>
        <el-form
            :model="editForm"
            label-width="130px"
            :rules="editFormRules"
            ref="editForm"
        >
            <el-row
                :gutter="10"
                style="margin-left: 0; margin-right: 0"
                type="flex"
            >
                <el-col :span="18">
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="监测对象名称" prop="sitename">
                                <el-input
                                    maxlength="500"
                                    v-model="editForm.sitename"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item
                                label="监测对象简称"
                                prop="siteshortname"
                            >
                                <el-input
                                    maxlength="12"
                                    show-word-limit
                                    v-model="editForm.siteshortname"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item
                                prop="siteajcode"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >安全监督备案号<font
                                        >(示范片区必填)</font
                                    ></label
                                >
                                <el-input
                                    maxlength="20"
                                    v-model="editForm.siteajcode"
                                    auto-complete="off"
                                    ><el-button
                                        slot="append"
                                        class="magic-btn"
                                        @click="getAqtProj"
                                        >同步安监通</el-button
                                    ></el-input
                                >
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="所属分组" prop="GROUPID">
                                <el-select
                                    v-model="editForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="changeGroup"
                                    :disabled="isEdit"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item
                                label="所属地区"
                                prop="sitecityregionblock"
                            >
                                <el-cascader
                                    :options="areas"
                                    :props="{
                                        checkStrictly: true,
                                    }"
                                    clearable
                                    v-model="editForm.sitecityregionblock"
                                    placeholder="请选择"
                                    class="sitecityregionblock"
                                    :key="keyValue"
                                ></el-cascader>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="项目类别" prop="sitetype">
                                <el-select
                                    v-model="editForm.sitetype"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in jclx"
                                        :key="item.DDID"
                                        :label="item.dataitem"
                                        :value="item.DDID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="监测对象地址" prop="siteaddr">
                                <el-input
                                    v-model="editForm.siteaddr"
                                    auto-complete="off"
                                    maxlength="500"
                                    ><el-button
                                        slot="append"
                                        icon="el-icon-location"
                                        class="locate-btn"
                                        @click="addrToLnglat"
                                    ></el-button
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="经纬度" prop="sitelng">
                                <el-row type="flex" justify="space-between">
                                    <el-col :span="12"
                                        ><el-input
                                            v-model="editForm.sitelng"
                                            auto-complete="off"
                                            maxlength="20"
                                        ></el-input
                                    ></el-col>
                                    <el-col :span="12">
                                        <el-input
                                            v-model="editForm.sitelat"
                                            auto-complete="off"
                                            maxlength="20"
                                        ></el-input
                                    ></el-col>
                                </el-row>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item
                                label="施工合同备案号"
                                prop="sitecode"
                            >
                                <el-input
                                    v-model="editForm.sitecode"
                                    auto-complete="off"
                                    type="tel"
                                    maxlength="30"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="计划日期" prop="planstartdate">
                                <el-row type="flex" justify="space-between">
                                    <el-col :span="12">
                                        <el-date-picker
                                            v-model="editForm.planstartdate"
                                            type="date"
                                            :picker-options="pickerOptionsStart"
                                            placeholder="开始日期"
                                            :clearable="false"
                                            class="daterange"
                                        >
                                        </el-date-picker
                                    ></el-col>
                                    <el-col :span="12">
                                        <el-date-picker
                                            v-model="editForm.planenddate"
                                            type="date"
                                            :picker-options="pickerOptionsEnd"
                                            placeholder="结束日期"
                                            :clearable="false"
                                            class="daterange"
                                        >
                                        </el-date-picker
                                    ></el-col>
                                </el-row>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="实际日期" prop="startdate">
                                <el-row type="flex" justify="space-between">
                                    <el-col :span="12">
                                        <el-date-picker
                                            v-model="editForm.startdate"
                                            type="date"
                                            :picker-options="pickerOptionsStart"
                                            placeholder="开始日期"
                                            :clearable="false"
                                            class="daterange"
                                        >
                                        </el-date-picker
                                    ></el-col>
                                    <el-col :span="12">
                                        <el-date-picker
                                            v-model="editForm.enddate"
                                            type="date"
                                            :picker-options="pickerOptionsEnd"
                                            placeholder="结束日期"
                                            :clearable="false"
                                            class="daterange"
                                        >
                                        </el-date-picker
                                    ></el-col>
                                </el-row>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"
                            ><el-form-item
                                label="施工类型"
                                prop="constructtype"
                            >
                                <el-select
                                    v-model="editForm.constructtype"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in sglx"
                                        :key="item.DDID"
                                        :label="item.dataitem"
                                        :value="item.dataitem"
                                    ></el-option>
                                </el-select> </el-form-item
                        ></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="项目经理" prop="contact">
                                <el-row type="flex" justify="space-between">
                                    <el-col :span="12">
                                        <el-input
                                            maxlength="20"
                                            v-model="editForm.contact"
                                            auto-complete="off"
                                            placeholder="姓名"
                                        ></el-input
                                    ></el-col>
                                    <el-col :span="12">
                                        <el-input
                                            maxlength="50"
                                            v-model="editForm.tel"
                                            auto-complete="off"
                                            placeholder="手机"
                                        ></el-input
                                    ></el-col>
                                </el-row>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"
                            ><el-form-item
                                label="项目经理身份证"
                                prop="contactidcard"
                            >
                                <el-input
                                    maxlength="18"
                                    v-model="editForm.contactidcard"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="8"
                            ><el-form-item label="集成商" prop="integrator">
                                <el-input
                                    maxlength="20"
                                    v-model="editForm.integrator"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                    </el-row>
                    <el-row> </el-row>
                    <el-row>
                        <el-col :span="8"
                            ><el-form-item
                                label="工程造价(万元)"
                                prop="sitecost"
                            >
                                <el-input
                                    maxlength="20"
                                    v-model="editForm.sitecost"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="8"
                            ><el-form-item
                                label="占地面积(㎡)"
                                prop="floorarea"
                            >
                                <el-input
                                    maxlength="50"
                                    v-model="editForm.floorarea"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="8"
                            ><el-form-item
                                label="建筑面积(㎡)"
                                prop="buildingarea"
                            >
                                <el-input
                                    maxlength="50"
                                    v-model="editForm.buildingarea"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="8">
                            <el-form-item
                                prop="projstatus"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >项目状态<font>(示范片区必填)</font></label
                                >
                                <el-select
                                    v-model="editForm.projstatus"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in mprojstatus"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item prop="isself" class="green-require">
                                <label slot="label" class="item-label"
                                    >是否自营<font>(示范片区必填)</font></label
                                >
                                <el-select
                                    v-model="editForm.isself"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option key="1" label="是" :value="1">
                                    </el-option>
                                    <el-option key="0" label="否" :value="0">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"
                            ><el-form-item prop="isgreen" class="green-require">
                                <label slot="label" class="item-label"
                                    >是否绿色示范片区<font
                                        >(示范片区必填)</font
                                    ></label
                                >
                                <el-select
                                    v-model="editForm.isgreen"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option key="1" label="是" :value="1">
                                    </el-option>
                                    <el-option key="0" label="否" :value="0">
                                    </el-option>
                                </el-select> </el-form-item
                        ></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item prop="appkey" class="green-require">
                                <label slot="label" class="item-label"
                                    >动态考核appkey<font
                                        >(示范片区必填)</font
                                    ></label
                                >
                                <el-input
                                    maxlength="50"
                                    v-model="editForm.appkey"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item prop="secret" class="green-require">
                                <label slot="label" class="item-label"
                                    >动态考核secret<font
                                        >(示范片区必填)</font
                                    ></label
                                >
                                <el-input
                                    maxlength="50"
                                    v-model="editForm.secret"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item
                                prop="url"
                                label="动态考核地址"
                                class="green-require"
                            >
                                <el-input
                                    show-word-limit
                                    v-model="editForm.url"
                                    disabled
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="6">
                    <Markmap
                        :markposition="markposition"
                        :centerstr="centerstr"
                        :addrinput="mapAddr"
                        @callMap="callAddMap"
                        @callLnglat="callLnglat"
                        style="height: calc(100% - 20px)"
                    ></Markmap>
                </el-col>
            </el-row>
            <el-row
                v-for="(item, index) in editForm.sitecompany"
                :key="item.companytype"
            >
                <el-col :span="6">
                    <el-form-item
                        :prop="`sitecompany[${index}].companytype`"
                        :label="item.companynamelbl"
                    >
                        <el-input
                            maxlength="100"
                            v-model="editForm.sitecompany[index].companyname"
                        ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item
                        :prop="`sitecompany[${index}].companycode`"
                        class="green-require"
                    >
                        <label slot="label" class="item-label"
                            >社会统一<br />信用代码</label
                        >
                        <el-input
                            maxlength="20"
                            v-model="editForm.sitecompany[index].companycode"
                        ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item
                        :prop="`sitecompany[${index}].companycontact`"
                        label="联系人"
                    >
                        <el-input
                            maxlength="50"
                            v-model="editForm.sitecompany[index].companycontact"
                        ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item
                        :prop="`sitecompany[${index}].companytel`"
                        label="联系方式"
                    >
                        <el-input
                            maxlength="100"
                            v-model="editForm.sitecompany[index].companytel"
                        ></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6"
                    ><el-form-item label="是否推送到市里" prop="bpush">
                        <el-select
                            v-model="editForm.bpush"
                            filterable
                            placeholder="请选择"
                        >
                            <el-option key="1" label="是" :value="1">
                            </el-option>
                            <el-option key="0" label="否" :value="0">
                            </el-option> </el-select></el-form-item
                ></el-col>
                <el-col :span="6">
                    <el-form-item label="是否市管项目" prop="bcityctrl">
                        <el-select
                            v-model="editForm.bcityctrl"
                            filterable
                            placeholder="请选择"
                        >
                            <el-option key="1" label="是" :value="1">
                            </el-option>
                            <el-option key="0" label="否" :value="0">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">&nbsp;</el-col>
                <el-col :span="6">
                    <div style="text-align: center">
                        <el-button @click.native="cancelAdd">返回</el-button>
                        <el-button
                            type="primary"
                            @click.native="editSubmit"
                            :loading="editLoading"
                            >提交</el-button
                        >
                    </div>
                </el-col>
            </el-row>
        </el-form>
    </section>
</template>
<script>
import {
    editSiteAndCompany,
    getsitecompany,
    getAreas,
    addSiteAndCompany,
    getAqtProj,
} from "../../api/api";
import Markmap from "../../components/MarkMap";
import dicmixin from "../../assets/dic/dicmixin";
import { mprovider } from "../../assets/dic/dic";
export default {
    components: { Markmap },
    mixins: [dicmixin],
    data() {
        return {
            groups: [],
            jclx: [],
            mprojstatus: [
                { id: 1, name: "待审" },
                { id: 2, name: "在建" },
                { id: 3, name: "停工" },
                { id: 4, name: "终止安监" },
                { id: 5, name: "竣工" },
            ],
            citys: [],
            districts: [],
            cityregionblocks: [], //兼容1.0
            editLoading: false,
            editFormRules: {
                sitename: [
                    {
                        required: true,
                        message: "请输入监测对象名称",
                        trigger: "blur",
                    },
                ],
                siteshortname: [
                    {
                        required: true,
                        message: "请输入监测对象简称",
                        trigger: "blur",
                    },
                ],
                GROUPID: [
                    {
                        required: true,
                        message: "请选择所属分组",
                        trigger: "blur",
                    },
                ],
                sitecityregionblock: [
                    {
                        required: true,
                        message: "请选择所属地区",
                        trigger: "blur",
                    },
                ],
                sitetype: [
                    {
                        required: true,
                        message: "请选择项目类别",
                        trigger: "blur",
                    },
                ],
                siteaddr: [
                    {
                        required: true,
                        message: "请输入地址",
                        trigger: "blur",
                    },
                ],
                sitelng: [
                    {
                        required: true,
                        message: "请输入项目经度",
                        trigger: "blur",
                    },
                ],
                sitelat: [
                    {
                        required: true,
                        message: "请输入项目纬度",
                        trigger: "blur",
                    },
                ],
                sitecode: [
                    {
                        required: true,
                        message: "请输入施工合同备案号",
                        trigger: "blur",
                    },
                ],
                startdate: [
                    {
                        required: true,
                        message: "请输入实际开始日期",
                        trigger: "blur",
                    },
                ],
                planstartdate: [
                    {
                        required: true,
                        message: "请输入计划开始日期",
                        trigger: "blur",
                    },
                ],
            },
            //编辑界面数据
            editForm: {
                // GroupId: "",
            },
            mprovider: mprovider,
            sglx: [],
            pickerOptionsStart: {
                disabledDate: (time) => {
                    if (this.editForm.enddate) {
                        return (
                            time.getTime() >
                            new Date(this.editForm.enddate).getTime()
                        );
                    }
                },
            },
            pickerOptionsEnd: {
                disabledDate: (time) => {
                    if (this.editForm.startdate) {
                        return (
                            time.getTime() <
                            new Date(this.editForm.startdate).getTime()
                        );
                    }
                },
            },
            areas: [],
            keyValue: 0,
            markposition: {},
            centerstr: "",
            isEdit: false,
            localSearch: null,
            mapAddr: {},
        };
    },
    watch: {
        areas(newVal) {
            this.keyValue++; //监听到数据源发生变化 ，改变keyValue的值，达到重新渲染的效果
        },
    },
    methods: {
        changeGroup(val) {
            let selG = this.groups.filter((g) => {
                return g.GROUPID == val;
            });
            if (selG && selG.length > 0) {
                this.getAreas(selG[0].GROUPID);
            }
        },
        callAddMap(item) {
            this.$set(this.editForm, "sitelng", item.point.lng);
            this.$set(this.editForm, "sitelat", item.point.lat);
            this.$set(this.editForm, "siteaddr", item.address);
        },
        callLnglat(item) {
            this.$set(this.editForm, "sitelng", item.point.lng);
            this.$set(this.editForm, "sitelat", item.point.lat);
        },
        // getCitys(pid) {
        //     let para = { pid: pid };
        //     City(para).then((res) => {
        //         if (res.success) {
        //             this.citys = res.data;
        //         }
        //     });
        // },
        getAreas(id) {
            let para = { GROUPID: id };
            getAreas(para).then((res) => {
                if (res.success) {
                    let result = res.data;
                    let areas = [];
                    //设置地图中心点
                    if (!this.isEdit) {
                        this.centerstr = result.citys[0].label;
                    }
                    areas.push(result.citys[0]);
                    //兼容1.0
                    this.cityregionblocks = [];
                    this.cityregionblocks.push(result.citys[0]);
                    let regions = JSON.parse(JSON.stringify(result.regions));
                    this.cityregionblocks.push(...regions);
                    let blocks = JSON.parse(JSON.stringify(result.blocks));
                    this.cityregionblocks.push(...blocks);
                    areas[0].children = [];
                    for (let i = 0; i < result.regions.length; i++) {
                        let children = [];
                        for (let k = 0; k < result.blocks.length; k++) {
                            if (
                                result.blocks[k].value.substr(0, 6) ==
                                result.regions[i].value
                            ) {
                                children.push(result.blocks[k]);
                            }
                        }
                        if (children.length > 0) {
                            result.regions[i].children = children;
                        }
                        areas[0].children.push(result.regions[i]);
                    }

                    this.editForm.sitecityregionblock = [];
                    this.areas = areas;
                    if (this.editForm.GROUPID != this.editForm.OLDGROUPID) {
                        //分组和以前保存的不一样
                        let sitecityregionblock = [];

                        sitecityregionblock.push(this.areas[0].value);
                        if (this.areas[0].children.length == 1) {
                            sitecityregionblock.push(
                                this.areas[0].children[0].value
                            );
                        }

                        this.editForm.sitecityregionblock = sitecityregionblock;
                    } else {
                        this.editForm.sitecityregionblock =
                            this.editForm.oldsitecityregionblock;
                    }
                }
            });
        },
        cancelAdd() {
            this.$router.replace(`/site/sites`);
        },
        backToList() {
            this.$router.replace(`/site/sites`);
            // window.location.reload();
            // this.$router.go(0);
        },
        addrToLnglat() {
            if (this.editForm.siteaddr) {
                let mapAddr = {};
                mapAddr.addr = this.editForm.siteaddr;
                mapAddr.date = new Date();
                this.mapAddr = mapAddr;
            }
        },
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    //市区街道加工
                    let cityregionblock = para.sitecityregionblock;
                    if (cityregionblock.length > 0) {
                        para.sitecity = cityregionblock[0];
                    } else {
                        para.sitecity = "";
                    }
                    if (cityregionblock.length > 1) {
                        para.siteregion = cityregionblock[1];
                    } else {
                        para.siteregion = "";
                    }
                    if (cityregionblock.length > 2) {
                        para.siteblock = cityregionblock[2];
                    } else {
                        para.siteblock = "";
                    }
                    //兼容过渡期间补上1.0用的sitearea字段
                    let area = [];
                    if (para.siteblock) {
                        for (let i = 0; i < this.cityregionblocks.length; i++) {
                            // console.log(para.siteblock);
                            // console.log(JSON.stringify(this.cityregionblocks));
                            if (
                                this.cityregionblocks[i].value == para.siteblock
                            ) {
                                para.sitearea =
                                    this.cityregionblocks[i].REGIONID;
                                break;
                            }
                        }
                    } else if (para.siteregion) {
                        for (let i = 0; i < this.cityregionblocks.length; i++) {
                            if (
                                this.cityregionblocks[i].value ==
                                para.siteregion
                            ) {
                                para.sitearea =
                                    this.cityregionblocks[i].REGIONID;
                                break;
                            }
                        }
                    } else if (para.sitecity) {
                        for (let i = 0; i < this.cityregionblocks.length; i++) {
                            if (
                                this.cityregionblocks[i].value == para.sitecity
                            ) {
                                para.sitearea =
                                    this.cityregionblocks[i].REGIONID;
                                break;
                            }
                        }
                    }
                    // //五方加工
                    // for (let i = 0; i < para.sitecompany.length; i++) {
                    //     para.sitecompany[i].GROUPID = para.GROUPID;
                    //     para.sitecompany[i].SITEID = para.SITEID;
                    // }
                    if (this.isEdit) {
                        //编辑
                        editSiteAndCompany(para).then((res) => {
                            if (res.success) {
                                this.editLoading = false;
                                this.$message({
                                    message: "修改成功",
                                    type: "success",
                                });
                                this.backToList();
                                this.clearGroupSites();
                            } else {
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }
                        });
                    } else {
                        //新建
                        addSiteAndCompany(para).then((res) => {
                            if (res.success) {
                                this.editLoading = false;
                                this.$message({
                                    message: "保存成功",
                                    type: "success",
                                });
                                this.backToList();
                                this.clearGroupSites();
                            } else {
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }
                        });
                    }
                }
            });
        },
        getSite() {
            getsitecompany({ SITEID: this.$route.params.id }).then((res) => {
                if (res.success) {
                    this.markposition = {
                        lng: res.data.sitelng,
                        lat: res.data.sitelat,
                    };
                    //市区街道转换
                    res.data.sitecityregionblock = [];
                    if (res.data.sitecity) {
                        res.data.sitecityregionblock.push(res.data.sitecity);
                    }
                    if (res.data.siteregion) {
                        res.data.sitecityregionblock.push(res.data.siteregion);
                    }
                    if (res.data.siteblock) {
                        res.data.sitecityregionblock.push(res.data.siteblock);
                    }
                    res.data.oldsitecityregionblock =
                        res.data.sitecityregionblock;
                    res.data.OLDGROUPID = res.data.GROUPID;

                    //五方转换
                    let companys = res.data.sitecompany;
                    let types = [];

                    for (let i = 0; i < companys.length; i++) {
                        types.push(companys[i].companytype);
                    }

                    for (let i = 1; i < 6; i++) {
                        if (types.indexOf(i) < 0) {
                            companys.push({
                                companytype: i,
                                companyname: "",
                                companycode: "",
                                companycontact: "",
                                companytel: "",
                            });
                        }
                    }

                    this.SetCompanyType(companys);

                    res.data.sitecompany = companys;
                    this.editForm = res.data;
                    this.getAreas(res.data.GROUPID);
                }
            });
        },
        SetCompanyType(companys) {
            for (let i = 0; i < companys.length; i++) {
                if (companys[i].companytype == 1) {
                    companys[i].companynamelbl = "建设单位";
                } else if (companys[i].companytype == 2) {
                    companys[i].companynamelbl = "施工单位";
                } else if (companys[i].companytype == 3) {
                    companys[i].companynamelbl = "监理单位";
                } else if (companys[i].companytype == 4) {
                    companys[i].companynamelbl = "勘察单位";
                } else {
                    companys[i].companynamelbl = "设计单位";
                }
            }

            companys.sort((a, b) => {
                return a.companytype - b.companytype;
            });

            return companys;
        },
        getAqtProj() {
            if (!this.editForm.siteajcode) {
                this.$message({
                    message: "请输入安监备案号",
                    type: "error",
                });

                return;
            }
            getAqtProj({ siteajcode: this.editForm.siteajcode }).then((res) => {
                if (res.success && res.data.length > 0) {
                    this.$set(
                        this.editForm,
                        "sitename",
                        res.data[0].projectName
                    );
                    this.$set(
                        this.editForm,
                        "siteaddr",
                        res.data[0].projectAddress
                    );
                    this.$set(this.editForm, "builder", res.data[0].sgdw);
                    this.$set(this.editForm, "constructor", res.data[0].jsdw);
                    this.$set(
                        this.editForm,
                        "sitecost",
                        res.data[0].projectPrice
                    );
                    this.$set(
                        this.editForm,
                        "floorarea",
                        res.data[0].projectAcreage
                    );
                    this.$set(
                        this.editForm,
                        "planstartdate",
                        res.data[0].projectStartDateTimne.substr(0, 10)
                    );
                    this.$set(
                        this.editForm,
                        "planenddate",
                        res.data[0].projectEndDateTimne.substr(0, 10)
                    );
                    this.editForm.sitecompany[0].companyname = res.data[0].jsdw;
                    this.editForm.sitecompany[0].companycode =
                        res.data[0].jsdwEntCode;
                    this.editForm.sitecompany[1].companyname = res.data[0].sgdw;
                    this.editForm.sitecompany[1].companycode =
                        res.data[0].sgdwEntCode;
                    this.editForm.sitecompany[2].companyname = res.data[0].jldw;
                    this.editForm.sitecompany[2].companycode =
                        res.data[0].jldwEntCode;
                    this.$set(
                        this.editForm,
                        "sitecompany",
                        this.editForm.sitecompany
                    );
                    this.$message({
                        message: "同步成功",
                        type: "success",
                    });
                } else {
                    this.$message({
                        message: "同步失败",
                        type: "error",
                    });
                }
            });
        },
    },
    mounted() {
        this.getGroups();
        // this.getCitys(0);
        this.sglx = this.$store.state.options.sglx;
        this.jclx = this.$store.state.options.jclx;
        //编辑
        if (this.$route.params.id > 0) {
            this.isEdit = true;
            this.getSite();
        } else {
            //新增
            this.isEdit = false;
            let companys = [];
            for (let i = 1; i < 6; i++) {
                companys.push({
                    companytype: i,
                    companyname: "",
                    companycode: "",
                    companycontact: "",
                    companytel: "",
                });
            }
            this.SetCompanyType(companys);
            // this.editForm.sitecompany = companys;
            this.$set(this.editForm, "sitecompany", companys);
        }
    },
};
</script>
<style scoped>
.bm-view {
    width: 100%;
    height: 100%;
}
.el-select,
.el-date-editor {
    width: 100%;
}
.green-require /deep/ .el-form-item__label {
    line-height: 20px !important;
}
.item-label font {
    color: red;
    display: block;
}
.daterange /deep/ .el-input__inner {
    padding-right: 5px;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner,
.el-cascader {
    width: 100%;
}
.sitecityregionblock /deep/ .el-radio {
    visibility: hidden;
}
section {
    margin-right: 15px;
}
.magic-btn {
    color: rgb(7, 185, 255) !important;
    /* font-weight: bold !important; */
    font-size: 10px;
    /* background: rgb(231, 231, 231) !important; */
}
.locate-btn {
    color: rgb(7, 185, 255) !important;
    font-size: 18px;
}
</style>


